
body {
  line-height: 1.4;
  color: #333;
  font-family: Helvetica, Tahoma, Arial, "PingFang SC", "Hiragino Sans GB",
    "Heiti SC", "Microsoft YaHei", "WenQuanYi Micro Hei";
  font-size: 12px;
  background: white;
}
input:focus,
select:focus,
textarea:focus,
button:focus {
  outline: none;
}
input {
  border: none;
}
.scale-b-1px {
  position: relative;
  border: none;
}
.scale-b-1px:after {
  content: "";
  position: absolute;
  bottom: 0;
  background: rgba(183, 183, 183, 0.2);
  width: 100%;
  height: 1px;
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
  -webkit-transform-origin: 0 0;
  transform-origin: 0 0;
}
.scale-1px {
  position: relative;
  margin-bottom: 20px;
  border: none;
}
.scale-1px:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  border: 1px solid #000;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  width: 200%;
  height: 200%;
  -webkit-transform: scale(0.5);
  transform: scale(0.5);
  -webkit-transform-origin: left top;
  transform-origin: left top;
}

.footer {
  width: 100%;
  height: 14vw;
  text-align: center;
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 9999;
  background: #fff;
}
.footer .footerList {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  background-color: #fff;
}
.footer .footerList .footerListitem {
  font-size: 18px;
  height: 11.8vw;
  background-color: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.footer .footerList .footerListitem .footerListitemImg {
  height: 10vw;
  width: 12vw;
  background-color: #ffc0cb;
  float: left;
}
.footer .footerList .footerListitem a {
  color: #7e8c8d;
  text-decoration: none;
  -webkit-backface-visibility: hidden;
}































